@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "HyliaSerif";
  src: url(/which-zelda/static/media/HyliaSerifBeta-Regular.a9519052.otf) format("opentype");
}

.App {
  text-align: center;
  height: 100%;
  margin-top: 0;
  padding-left: 4%;
  padding-right: 4%;
  overflow: auto;
  background-position: center center;
  background-size: 2.5rem, cover;
  background-image: linear-gradient(to bottom, #023326 0%, #053335 50%, #0a1928 100%);
  background-repeat: repeat, no-repeat;

  font-family: 'HyliaSerif', Arial, sans-serif;
  color: #ffffff;
}

.App-link {
  color: #61dafb;
}

h1, h2, h3, h4, p, li {
  font-weight: 100;
}

p, li {
  font-family: 'Roboto', sans-serif;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 18px;
  max-width: 10%;
  margin: 10px;
  cursor: pointer;
  background-color: #1d578b;
  position: relative;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.6);
  border-radius: 18px;
}

.card .img {
  max-width: 100%;
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  border-radius: 18px;
}

.card .img-gray {
  max-width: 100%;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  border-radius: 18px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  /* border-radius: 18px; */
  transition: .5s ease;
  background-color: #008CBA;
}

.card:hover .overlay {
  opacity: 0.5;
  border-radius: 18px;
}

footer {  
  font-size: 13px;
}

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted #fff; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #000;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}
